body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login{
    text-align: right;
    font-size: 20px;
    
}
.header{
    font-family:  Verdana,Arial,sans-serif;
}
.container{
    display: grid;
    grid-template-columns: 300px 950px;
    background-color: rgb(82, 76, 74);
    min-height: 600px;
}

.col1{
    background-color: rgb(82, 76, 74);
    color:white;
}

.col2{
    margin: 2px;
    background-color: rgb(117, 112, 111);
}
   

table {
    border-collapse: collapse;
  }
  
  table, th, td {
    table-layout: fixed;
    border: 1px solid rgb(87, 84, 84);
    color:white;
    width: 300px;
    height: 60px;
  }

  td:hover { 
    background-color:rgb(150, 224, 169);
  }
  

.content {
    margin: 25px;
    width: 100%;
    color:white;
    display: grid;
    grid-template-columns: 20px 20px 20px 20px;
  }
  
.Content{
    margin-top: 16px;
    
}
.Header{
    
    background-color: rgb(34, 165, 110);
    min-height: 16vh;
   
    height: 10vmin;
    font-size: calc(35px + 4vmin);

}

